<style>
  
</style>

<template>
  <div>
    <div class="add-form-wraper">
      <el-form ref="form" :model="addParamMesg" label-width="160px">
        <el-form-item label="销售平台">
          <p style="font-size:16px !important;color:#555;font-weight:bold;">{{addParamMesg.platformName}}</p>
        </el-form-item>

        <el-form-item label="开启配送" required v-if="platformId == 5">
          <el-radio-group v-model="addParamMesg.openDc" size="small" @change="openDcChange">
            <el-radio-button label="1">是</el-radio-button>
            <el-radio-button label="0">否</el-radio-button>
          </el-radio-group>
        </el-form-item>
        
        <el-form-item label="立即送时间" required v-if="platformId == 5 && addParamMesg.openDc === 1">
          <el-time-picker
            is-range
            class="add-input"
            v-model="addParamMesg.dcTimeTemp"
            range-separator="-"
            start-placeholder="开始时间"
            size="small"
            format="HH:mm"
            value-format="HH:mm"
            end-placeholder="结束时间">
          </el-time-picker>
        </el-form-item>

        <el-form-item label="配送范围" required v-if="platformId == 5 && addParamMesg.openDc === 1">
          <el-input v-model="addParamMesg.dcScope" size="small" class="add-input" placeholder="请输入配送范围">
            <el-button slot="prepend">半径</el-button>
            <el-button slot="append">公里</el-button>
          </el-input>
          <span>配送范围为大于0的整数或小数</span>
        </el-form-item>

        <el-form-item label="关联三方门店ID" required v-if="platformId != 5">
          <el-input v-model="addParamMesg.outerPlatformId" size="small" class="add-input" placeholder="请输入三方门店ID"></el-input>
        </el-form-item>
        
        <!-- <el-form-item label="是否开启线上业务" required>
          <el-radio-group v-model="addParamMesg.openOnline" size="small">
            <el-radio-button :label="item.label" v-for="(item, idx) in openOnlineRadioOpt" :key="idx">{{item.labelName}}</el-radio-button>
          </el-radio-group>
        </el-form-item> -->

        <el-form-item label="营业状态" required>
          <el-radio-group v-model="addParamMesg.openStatus" size="small">
            <el-radio-button :label="item.label" v-for="(item, idx) in openStatusRadioOpt" :key="idx">{{item.labelName}}</el-radio-button>
          </el-radio-group>
        </el-form-item>
        

        <hr class="s-slip-tb">
        <el-form-item>
          <el-button type="primary" size="small" @click="doSaveHandel" :disabled="isDisabled">保存</el-button>
          <el-button size="small" @click="doCancelHandel">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import Storage from '@/utils/localStorage'
  const storeMGTUrl = {
    // 店铺信息
    platformInfo: '/plateform/a/store/platform/info',
    // 上线保存
    platformOnline: '/plateform/a/store/platform/online'
  }
  export default {
    data() {
      return {
        isDisabled: false, // 防止多次点击
        // 平台id 
        platformId: '',
        // 提交信息
        addParamMesg: {
          platform: '', // 三方平台：1-有赞，2-京东到家，3-美团外卖，4-饿了么, 5-本来鲜
          platformName: '',
          openDc: 1, // 是否开启配送：0-未开启;1-已开启
          // 可配送时间  一个开始时间一个结束时间
          dcTimeTemp: ['', ''], 
          // 开始时间
          dcOpenTime: '',
          // 结束时间
          dcCloseTime: '',
          dcScope: '', // 配送范围
          storeId: '', // 门店id
          outerPlatformId: '', // 三方门店id
          storeNo: '', // 门店编号
          // openOnline: 1, // 开启线上业务：0-未开启;1-已开启
          openStatus: 1 // 营业状态：1-营业中;2-歇业中
        },
        // openOnlineRadioOpt: [
        //   {
        //     label: 1,
        //     labelName: '开启'
        //   },
        //   {
        //     label: 0,
        //     labelName: '禁用'
        //   },
        // ],
        openStatusRadioOpt: [
          {
            label: 1,
            labelName: '营业中'
          },
          {
            label:2,
            labelName: '歇业中'
          }
        ]

      }
    },
    created() {
      this.platformId = this.$route.params.platformId
      this.addParamMesg.platform = this.$route.params.platformId
      this.addParamMesg.storeId = this.$route.params.storeId
      this.addParamMesg.storeNo = this.$route.params.storeNo
      if(this.platformId == 1) {
        this.addParamMesg.platformName = '有赞'
      }else if(this.platformId == 2) {
        this.addParamMesg.platformName = '京东到家'
      }else if(this.platformId == 3) {
        this.addParamMesg.platformName = '美团外卖'
      }else if(this.platformId == 4) {
        this.addParamMesg.platformName = '饿了么'
      }else if(this.platformId == 5) {
        this.addParamMesg.platformName = '本来鲜'
      }

      this.getDetailMesg()
      
    },

    methods: {
      /**
       * [openDcChange 开启配送切换]
       * @param  {[type]} _val [description]
       * @return {[type]}      [description]
       */
      openDcChange(_val) {
        this.addParamMesg.openDc = parseInt(_val)
        // 未开启线上业务 配送时间和范围置空
        if(_val == 0) {
          this.addParamMesg.dcTimeTemp = ['', '']
          this.addParamMesg.dcOpenTime = ''
          this.addParamMesg.dcCloseTime = ''
          this.addParamMesg.dcScope = ''
        }
      },
      /**
       * [getDetailMesg 获取详情]
       * @return {[type]} [description]
       */
      getDetailMesg() {
        utils.showLoading()
        this.$http.post(storeMGTUrl.platformInfo, {platformId: this.platformId, storeNo: this.addParamMesg.storeNo}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {             
             if(res.data) {
              this.addParamMesg.platform = res.data.platform
              this.addParamMesg.platformName = res.data.platformName
              if(!res.data.openStatus) {
                this.addParamMesg.openStatus = 1
              }else {
                this.addParamMesg.openStatus = parseInt(res.data.openStatus)
              }
  
              this.addParamMesg.outerPlatformId = res.data.outerPlatformId
              if(!res.data.openDc) {
                this.addParamMesg.openDc = 0
              }else {
                this.addParamMesg.openDc = parseInt(res.data.openDc)
              }
              if(res.data.dcOpenTime) {
                this.addParamMesg.dcTimeTemp[0] = res.data.dcOpenTime
                this.addParamMesg.dcOpenTime = res.data.dcOpenTime
              }
              if(res.data.dcCloseTime) {               
                this.addParamMesg.dcTimeTemp[1] = res.data.dcCloseTime
                this.addParamMesg.dcCloseTime = res.data.dcCloseTime
              }
              // if(!res.data.openOnline) {
              //   this.addParamMesg.openOnline = 0
              // }else {
              //   this.addParamMesg.openOnline = parseInt(res.data.openOnline)
              // }
              // 未开启线上业务 配送时间和范围置空
              if(this.addParamMesg.openDc == 0) {
                this.addParamMesg.dcTimeTemp = ['', '']
                this.addParamMesg.dcOpenTime = ''
                this.addParamMesg.dcCloseTime = ''
                this.addParamMesg.dcScope = ''
              }

              this.addParamMesg.dcScope = res.data.dcScope
              this.addParamMesg.dcTimeTemp = JSON.parse(JSON.stringify(this.addParamMesg.dcTimeTemp))
             }

            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doSaveHandel 保存]
       * @return {[type]} [description]
       */
      doSaveHandel() {
        // 1、如果是本来鲜  是否立即开启配送openDc、立即配送时间dcOpenTime和dcCloseTime、配送范围dcScope必填
        // 2、其他非本来鲜  三方门店id必填
        // 3、openStatus 营业状态必填
        let numReg = /^\d+(?=\.{0,1}\d+$|$)/ 
        if(this.addParamMesg.platform === '') {
          this.$message("请选择平台")
          return
        }
        if(this.addParamMesg.dcTimeTemp[0] && this.addParamMesg.dcTimeTemp[0] != '') {
          this.addParamMesg.dcOpenTime = this.addParamMesg.dcTimeTemp[0]
        }
        if(this.addParamMesg.dcTimeTemp[1] && this.addParamMesg.dcTimeTemp[1] != '') {
          this.addParamMesg.dcCloseTime = this.addParamMesg.dcTimeTemp[1]
        }
        // 本来鲜
        if(this.platformId == 5) {
          if(this.addParamMesg.openDc === '') {
            this.$message("请选择是否立即开启配送")
            return
          }

          if(this.addParamMesg.openDc === 1) {
            if(this.addParamMesg.dcOpenTime === '') {
              this.$message("请选择可配送开始时间")
              return
            }
            if(this.addParamMesg.dcCloseTime === '') {
              this.$message("请选择可配送结束时间")
              return
            }
            let dcOpenTimeNum = utils.changeStrToMinutes(this.addParamMesg.dcOpenTime)
            let dcCloseTimeNum = utils.changeStrToMinutes(this.addParamMesg.dcCloseTime)
            if(dcCloseTimeNum <= dcOpenTimeNum) {
              this.$message("配送结束时间不能早于开始时间")
              return
            }
            if(this.addParamMesg.dcScope === '') {
              this.$message("请选择可配送范围")
              return
            }else {
              if(!numReg.test(this.addParamMesg.dcScope)) {
                this.$message("可配送范围为大于0数字")
                return
              }else {
                if(parseFloat(this.addParamMesg.dcScope) < 0) {
                  this.$message("可配送范围为大于0数字")
                  return
                }
              }
            }
          }
        }else {
          // 第三方平台
          if(this.addParamMesg.outerPlatformId === '') {
            this.$message("请输入三方门店ID")
            return
          }
        }
        // if(this.addParamMesg.openOnline === '') {
        //   this.$message("请选择是否开启线上业务")
        //   return
        // }
        if(this.addParamMesg.openStatus === '') {
          this.$message("请选择营业状态")
          return
        }

        utils.showLoading()
        this.isDisabled = true
        this.$http.post(storeMGTUrl.platformOnline, {...this.addParamMesg}).then(res => {
          utils.clearLoading()
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("开启线上业务成功")
            this.$router.push("/blx_store")
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doCancelHandel 取消保存]
       * @return {[type]} [description]
       */
      doCancelHandel() {
        this.$confirm('确定取消开启线上业务吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/blx_store')
        }).catch(() => {})
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }


  }
</script>


